@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,600;0,700;1,400;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.correctLetter{color:var(--bg-color)}.incorrectLetter{color:#d4464d}

.word-box{height:150px;line-height:50px;padding:15px;font-size:24px;text-align:center;overflow:hidden;-ms-user-select:none;user-select:none;-moz-user-select:none;-khtml-user-select:none;-webkit-user-select:none;-o-user-select:none}@media screen and (min-width: 1920px){.word-box{font-size:28px}}.word-box div{border-radius:3.33px;display:inline-block;padding:0 10px}.word-box .active{background-color:#67bf6d;color:white;border:1px solid #18301a}.word-box .correct{color:#67bf6d}.word-box .mistake{color:#d4464d}

.input-box{text-align:center}.input-box .user-input{background-color:#0d1016;width:100%;padding:15px 0;border:none;text-align:center;color:white;font-size:24px;outline:none}.input-box .user-input:focus{border-bottom:1px solid white}@media screen and (min-width: 1920px){.input-box .user-input{font-size:28px}}

.stats-container{background-color:#0d1016;padding:0 5rem;display:flex;justify-content:space-between}.stats-container .custom-btn{display:block;position:absolute}.stats-container div{text-align:center}.stats-container div h1{font-family:'Playfair Display', serif;font-size:28px;opacity:.3}.stats-container div h2{font-size:14px;line-height:0;opacity:.25;font-weight:400;padding:5px}

.results-container{font-family:var(--serif-font);margin:0 auto;text-align:center}.results-container .results-title{font-size:28px;font-weight:400;margin:50px 0 0 0}.results-container .wpm{font-size:38px;font-weight:700;margin:0;color:#ff4d4d;background:-webkit-linear-gradient(left, #ff4d4d, #ffa44f, #ff4d4d);background-size:1600px 200px;background-clip:text;-webkit-background-clip:text;-webkit-text-fill-color:transparent;animation-duration:3s;animation-name:WPMGradient;animation-iteration-count:infinite;animation-fill-mode:forwards}@keyframes WPMGradient{0%{background-position:0 1600px}100%{background-position:1600px 0}}.results-container .results{position:relative;display:flex;justify-content:space-around;margin:50px}.results-container .results .information-text{font-family:var(--sans-font);background-color:var(--light-bg);color:rgba(255,255,255,0.75);padding:15px;border-radius:3.33px;text-align:left;width:15%;box-shadow:0px 2px 15px 5px rgba(0,0,0,0.1);opacity:1}.results-container .results h2{font-size:32px}.results-container .results h3{font-family:var(--sans-font);font-weight:400;opacity:.5}.results-container .results h3 .information{font-size:12px;padding:0 5px 2px 5px;cursor:pointer}.results-container .restart{display:inline-block;font-size:28px;font-weight:400;font-style:italic;padding:20px;cursor:pointer}.results-container .restart:hover .arrow{opacity:.75;transform:translateX(20px)}.results-container .restart .arrow{opacity:0;transform:translateX(-25px);transition:all .2s ease-out}

:root{--bg-color: #0d1117;--light-bg: #161b24;--text-color: #ffffff;--sans-font: 'Source Sans Pro', sans-serif;--serif-font: 'Playfair Display', serif}body{background-color:#0d1117;background-color:var(--bg-color);color:#ffffff;color:var(--text-color);font-family:'Source Sans Pro', sans-serif;font-family:var(--sans-font)}body .app{margin:0 auto;padding:75px 0}@media screen and (min-width: 768px){body .app{width:75%}}@media screen and (min-width: 1280px){body .app{width:55%}}@media screen and (min-width: 1920px){body .app{width:40%}}body .app .title{font-family:'Playfair Display', serif;font-family:var(--serif-font);text-align:center;font-weight:600}body .app .heading{font-size:18px;font-family:'Playfair Display', serif;font-family:var(--serif-font);font-weight:400;text-align:center;opacity:.75}@media screen and (min-width: 1920px){body .app .heading{font-size:21px}}body .app .box{border-radius:5px;background-color:#161b24;background-color:var(--light-bg);margin:75px auto}

